<template>
  <div v-loading.fullscreen="loading" class="g-listpage__style">
    <div class="action-btn-container.space-inner">
      <div class="input-group"  >
      <el-select clearable v-model="searchForm.organizationId" placeholder="请选择组织" class="value-select"
              @change="changeOrgan" style="margin-right: 20px" >
              <el-option
                  v-for="item in companies"
                  :key="item.organizationId"
                  :label="item.organizationName"
                  :value="item.organizationId">
                </el-option>
      </el-select>
      <el-select clearable v-model="searchForm.roleId" placeholder="请选择角色" class="value-select"
             style="margin-right: 20px">
              <el-option
                  v-for="item in roles"
                  :key="item.roleId"
                  :label="item.roleName"
                  :value="item.roleId">
                </el-option>
      </el-select>
        <el-input class="right search-btn" v-model.trim="searchForm.inputs"  placeholder="请输入关键词">
          <i class="iconfont-search el-input__icon" style="vertical-align: middle" slot="suffix" @click="searchFn"></i>
        </el-input>
      </div>
      <div class="right-top" >
        <el-button type="primary" @click="addFuc"> 
          <span>+ 新增</span>
        </el-button>
        <el-button  @click="deleteFuc()" :disabled="!this.isHaveSelected">
          <i class="iconfont-remove-color" :class="{'icon-disable':!this.isHaveSelected}"></i>
          <span>删除</span>
        </el-button>
        <el-button  @click="updateStatus(1)" :disabled="!this.isHaveSelected">
          <i class="iconfont-start-color" :class="{'icon-disable':!this.isHaveSelected}"></i>
          <span>启用</span>
        </el-button>
        <el-button  @click="updateStatus(0)" :disabled="!this.isHaveSelected">
          <i class="iconfont-stop-color" :class="{'icon-disable':!this.isHaveSelected}"></i>
          <span>停用</span>
        </el-button>
      </div>
    </div>
    <el-table :data="tableData" border class="g-table"
     @selection-change="handleSelectionChange"
     row-key="userId" ref="table">
      <el-table-column type="selection" width="37" :reserve-selection="true">
      </el-table-column>
      <el-table-column type="index" label="序号" width="50" :index="indexMethod">
      </el-table-column>
      <!-- <el-table-column prop="userId" label="用户id">
      </el-table-column> -->
      <el-table-column prop="realname" label="姓名">
      </el-table-column>
      <el-table-column prop="mobile" label="手机号">
      </el-table-column> 
      <el-table-column prop="username" label="账号">
      </el-table-column>
      <el-table-column prop="organizationName" label="机构名称">
      </el-table-column>
      <el-table-column prop="roleName" label="角色名称">
      </el-table-column>
      <el-table-column prop="email" label="邮箱" >
      </el-table-column>
      <el-table-column prop="status" label="启用状态" >
      </el-table-column>
      <el-table-column prop="updateName" label="更新人" >
      </el-table-column>
      <el-table-column prop="updateTime" label="更新时间" >
      </el-table-column>
       <el-table-column label="操作" width="150">
        <template slot-scope="scope">
          <div class="actions">
            <button @click="editOrgInfo(scope.row)" >修改</button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination 
    style="float:middle"
    @size-change="pageSizeChange" 
    @current-change="pageNoChange" 
    :current-page="pageNo" 
    :page-sizes="[10, 20, 30, 40]" 
    :page-size="10" 
    layout="prev, pager, next, jumper,total, sizes" 
    :total="total">
    </el-pagination>
  </div>
</template>
<script>
import { api } from "@/api";
import util from "@/util/util";
import { formatDate } from "../../common/date.js";
export default {
  data() {
    return {
      companies: [],
      roles: [],
      isHaveSelected: false,
      formLabelWidth: "120px",
      dialogTitle: "",
      loading: false,
      date: "",
      total: 0,
      pageSize: 10,
      pageNo: 1,
      tableData: [],
      title: "新增",
      arbFormulaInfo: null,

      searchForm: {
        inputs: "",
        organizationId: "",
        roleId: ""
      },
      multipleSelection: [],
      selectDicValue: "",
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now();
        }
      }
    };
  },
  methods: {
    handleSelectionChange(val) {
      // console.log(val);
      let that = this;
      if (val.length == 0) {
        that.isHaveSelected = false;
      } else {
        that.isHaveSelected = true;
      }
      this.multipleSelection = val;
    },
    indexMethod(index) {
      return this.pageSize * (this.pageNo - 1) + index + 1;
    },
    pageSizeChange(size) {
      this.pageSize = size;
      this.getUserList();
    },
    pageNoChange(pageNo) {
      this.pageNo = pageNo;
      this.getUserList();
    },
    dicSelsctChange(value) {
      console.log(value);
    },

    changeOrgan(rule) {
      //切换重置角色
      if (!rule || rule !== true) {
        this.searchForm.roleId = "";
      }
      this.searchForm.roleId = "";
      api
        .getRoleListByOrgId({ organizationId: this.searchForm.organizationId })
        .then(
          data => {
            this.roles = data.data.list;
          },
          err => {
            console.log(err);
            this.$message.error("角色信息加载失败");
          }
        );
    },

    addFuc() {
      this.$router.push({
        path: "/admin/settings/editUser/add"
      });
    },

    editOrgInfo(row) {
      this.$router.push({
        path: "/admin/settings/editUser/" + row.userId
      });
    },
    deleteFuc() {
      let params = [];
      params = this.multipleSelection.map(item => {
        return item.userId;
      });
      let msg = "确认删除已选的用户？";

      this.$confirm(msg, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          api.deleteUser({ userIds: params.join(",") }).then(res => {
            if (res.data.code === 0) {
              this.$message.success("删除成功！");
              this.$refs.table.clearSelection();
              this.getUserList();
            } else {
              this.$message.error(res.data.msg);
            }
          });
        })
        .catch(() => {});
    },

    updateStatus(status) {
      let params = [];
      params = this.multipleSelection.map(item => {
        return item.userId;
      });

      let msg = status == 1 ? "确认启用已选的用户？" : "确认停用已选的用户";

      this.$confirm(msg, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          api
            .updateUserState({
              userIds: params.join(","),
              status: status
            })
            .then(res => {
              if (res.data.code === 0) {
                this.$message.success(
                  status == 1 ? "启用成功！" : "停用成功！"
                );
                this.$refs.table.clearSelection();
                this.getUserList();
              } else {
                this.$message.error(res.data.msg);
              }
            });
        })
        .catch(() => {});
    },

    getUserList() {
      api
        .getUserList({
          page: this.pageNo,
          limit: this.pageSize,
          queryName: this.searchForm.inputs,
          organizationId: this.searchForm.organizationId,
          roleId: this.searchForm.roleId
        })
        .then(res => {
          if (res.data.code == 0) {
            let data = res.data.page.list;
            if (data) {
              data.forEach(v => {
                v.status = v.status == 1 ? "启用" : "停用";
                var date = new Date(v.updateTime);
                v.updateTime = formatDate(date, "yyyy-MM-dd hh:mm:ss");
              });
            }

            this.tableData = data;
            this.total = res.data.page.totalCount;
          } else {
            this.tableData = [];
            this.total = 0;
          }
        });
    },

    searchFn() {
      this.pageNo = 1;
      this.getUserList();
    }
  },
  created() {
    this.getUserList();

    api
      .getOrgListForSelect()
      .then(
        data => {
          // if (!this.user.organizationId) {
          //   // 默认选中第一个组织
          //   let defaultCheckOrgan = _.first(data.data);
          //   if (defaultCheckOrgan) {
          //     this.user.organizationId = defaultCheckOrgan.organizationId;
          //     if (this.$route.params.id === "add") {
          //       this.changeOrgan(false);
          //     }
          //   }
          // }
          this.companies = data.data.list;
        },
        () => {}
      );
  },
  mounted() {}
};
</script>
<style lang="scss" scoped>
.formula-form {
  width: 200px;
  margin-right: 20px;
}
.actions {
  color: #0000ff;
  button {
    color: #0000ff;
    border: none;
    background: none;
    cursor: pointer;
    outline: none;
    &[disabled] {
      cursor: no-drop;
      color: #999;
    }
  }
}
</style>



